#footer{
    background-color: #002d46;
    color: aliceblue;
    .coontainer{
        padding-top: 4.3rem !important;
        .brand{
            @media (max-width:800px) {
                justify-content: center;
                align-items: center;
                display: flex;
                flex-direction: column;
            }
            display: flex;
            justify-content: start;
            flex-direction: column;
            padding-right: 1.5rem;
            img{
                max-width: min(100%, 200px);
            }
            P{
                font-size: 14px;
                font-weight: 200;
                letter-spacing: .04rem;
                font-family: var(--secondFont);
                text-align: justify;
                // margin-right: 4rem;
            }
        }

        .details{
            .items{
                p{
                    font-size: 18.5px;
                    font-weight: 800;
                    color: #00B9AA;
                    padding-bottom: .7rem;
                }
                ul{
                    list-style: none;
                    margin: 0;
                    padding: 0;
                    li{
                        margin-bottom: 3px !important;
                        a{
                            text-decoration: none;
                            color: #ffffff;
                            font-size: 14px;
                            font-weight: 400;
                            position: relative;
                        }
                        a::before {
                            content: '';
                            position: absolute;
                            width: 100%;
                            height: 1px;
                            border-radius: 2px;
                            background-color: rgb(255, 255, 255);
                            bottom: -1px;
                            left: 0;
                            transform-origin: right;
                            transform: scaleX(0);
                            transition: transform .4s ease-in-out;
                          }
                          
                          a:hover::before {
                            transform-origin: left;
                            transform: scaleX(1);
                          }
                    }
                }
            }
        }
        .get-in-touch{
            padding-left: 2rem !important;
            p{
                font-size: 18.5px;
                font-weight: 800;
                color: #00B9AA;
                padding-bottom: .8rem;
            }
            .contact{
                padding: 13px 0;
            }
            span{
                color: #ffffff;
                font-size: 15px;
                font-weight: 400;                
            }
            h5{
                font-size: 17px;
            }
            .email{
                margin: 0;
                padding: 0 0 0 0.9rem;
                input{
                     width: 100%;
                     height: 2.9rem;
                     border: 0;
                } 
                input::placeholder{
                    font-style: italic;
                    padding-left: .9rem;
                    color: #b6b6b6;
                }           
                input:focus{
                    border: 0;
                }
            }
            .subscribe{
                button{
                    width: auto;
                    height: 2.9rem;
                    margin: 0;
                    padding: 0 1.1rem;
                    border: 1.5px solid white;
                    background-color: transparent;
                    border-radius: 5px;
                    color: white;
                    font-size: 15px;
                    font-weight: 700;
                }
            }
        }
        .copywrite{
            p{
                font-family: var(--secondFont);
                font-weight: 200;
                @media (max-width:992px) {
                    padding-left: 1rem;
                }
            }
        }
    }
}


