.coontainer{
    width: 80% !important;
    margin: 0 auto;
    @media (max-width:768px) {
        width: 90% !important;
        margin: 0 auto;
    }
}

.main-nav{
    height: 5rem;
    color: #fafafa;
    display: flex;
    @media (max-width:1080px) {
        height: 4rem;
    }
    .logo{
        width: 20%;
        display: grid;
        @media (max-width:1080px) {
            width: 65%;
            grid-template-columns: 1fr 5fr;
        }
        button{
            display: none;
            @media (max-width:1080px) {
                display: block;
                border: 0;
                background-color: transparent;
                img{
                    width: 32px;
                    height: 32px;
                }
            }
        }
        a{
            @media (max-width:1080px) {
                justify-content: center;
                display: flex;
            }
            img{
                width: 155px;
                float: left;
                @media (max-width:1080px) {
                    width: 143px;
                    grid-column: 2/3;
                }
                @media (max-width:768px) {
                    width: 115px;
                }
            }
        }
    }
    // logo and hamburgers part ends

    #main-links{
        width: 75%;
        display: flex;
        justify-content: center;
        @media (max-width:1080px) {
            display: none;
        }
        ul{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            ol{
                list-style: none;
                gap: 2.4rem;
                display: flex;
                li{
                    a{
                        text-decoration: none;
                        color: #002D46;
                        font-size: .95rem;
                        font-weight: 700;
                        margin-bottom: 0 !important;
                        position: relative;
                    }
                    a::before {
                        content: '';
                        position: absolute;
                        width: 100%;
                        height: 1.5px;
                        border-radius: 1px;
                        background-color: lightgreen;
                        bottom: -1px;
                        left: 0;
                        transform-origin: right;
                        transform: scaleX(0);
                        transition: transform .3s ease-in-out;
                    }
                    a:hover::before {
                    transform-origin: left;
                    transform: scaleX(1);
                    }
                    
                    img{
                        display: none;
                    }
                }
            }
            .nav-bottom{
                display: none;
            }
        }
    }

    // resposive part remaining
    #mobile-showData{
        @media (max-width:1080px) {
            ul{
                display: flex;
                justify-content: left;
                align-items: top;
                width: 100%;
                position: absolute;
                top: 4rem;
                left: 0;
                background-color: rgba(0, 0, 0, 0.4);
                padding: 0;
                gap: 2rem;
                height: calc(100vh - 4rem);
                transition: all 0.3s ease;
                .for-mobile{
                    z-index: 999;
                    width: 50%;
                    height: 100vh;
                    background-color: white;
                    padding-top: 1rem;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    ol{
                        list-style: none;
                        display: flex;
                        flex-direction: column;
                        padding: 0;
                        li{
                            text-align: left;
                            padding: 10px 0 3px 1.5rem;
                            display: flex;
                            justify-content: space-between;
                            a{
                                text-decoration: none;
                                color: #002D46;
                                font-size: 1rem;
                                font-weight: 600;
                                margin-bottom: 0 !important;
                                position: relative;
                            }
                            a::before {
                                content: '';
                                position: absolute;
                                width: 100%;
                                height: 1.5px;
                                border-radius: 1px;
                                background-color: #2da0df69;
                                bottom: -1px;
                                left: 0;
                                transform-origin: right;
                                transform: scaleX(0);
                                transition: transform .3s ease-in-out;
                            }
                            a:hover::before {
                            transform-origin: left;
                            transform: scaleX(1);
                            }
                            a:hover{
                                font-weight: 600;
                            }
                            img{
                                display: flex;
                                width: 24px;
                                height: 20px;
                                margin-right: 1rem;
                            }
                        }
                        li:hover{
                            a{
                                font-weight: bolder;
                                transition: .25s;
                                letter-spacing: .5px;
                            }
                            img{
                                width: 26px;
                                height: 20px;                               
                            }
                        }
                    }
                }
            }
        }
        @media (max-width:768px) {
            ul{
                width: 100%;
                .for-mobile{
                    width: 80%;
                }
            }
        }
    }

    // ends of links part

    .main-icons{
        width: 35%;
        display: flex;
        align-items: center;
        @media (max-width:1080px) {
            width: 30%;
        }
        ul{
            display: flex;
            list-style: none;
            gap: 1.3rem;
            float: right;
            margin-bottom: 0;
            margin-left: 2rem;
            @media (max-width:1080px) {
                margin-left: 0%;
                padding-left: 18px;
            }
            li{
                list-style: none;
                a{
                    text-decoration: none;
                    cursor: pointer;
                    img{
                        width: 21.5px;
                        height: 21.5px;
                    }
                }
            }
        }
    }
}